// Dashboard.js
import React, { useState, useEffect, useRef } from "react";
import { signOut } from "firebase/auth";
import { doc, getDoc, setDoc, updateDoc } from "firebase/firestore";
import { auth, db } from "../firebase";
import imageCompression from 'browser-image-compression';
import "./Dashboard.css";

function Dashboard({ onLogout, user }) {
  const [profile, setProfile] = useState(null);
  const [isEditing, setIsEditing] = useState(false);
  const [uploading, setUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState(0);
  const [uploadError, setUploadError] = useState(null);
  const [uploadSuccess, setUploadSuccess] = useState(false);
  const [compressionInfo, setCompressionInfo] = useState(null);
  const [showUserMenu, setShowUserMenu] = useState(false);
  const fileInputRef = useRef(null);
  const userMenuRef = useRef(null);
  const [formData, setFormData] = useState({
    displayName: "",
    firstName: "",
    lastName: "",
    phone: "",
    email: "",
    location: "",
    state: "",
    city: "",
    pincode: "",
    avatar: "",
    bio: ""
  });

  useEffect(() => {
    if (user) {
      fetchUserProfile();
    }
  }, [user]);

  // Close user menu when clicking outside
  useEffect(() => {
    function handleClickOutside(event) {
      if (userMenuRef.current && !userMenuRef.current.contains(event.target)) {
        setShowUserMenu(false);
      }
    }

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  const fetchUserProfile = async () => {
    try {
      const docRef = doc(db, "users", user.uid);
      const docSnap = await getDoc(docRef);
      
      if (docSnap.exists()) {
        const profileData = docSnap.data();
        setProfile(profileData);
        setFormData({
          displayName: profileData.displayName || user.displayName || "User",
          firstName: profileData.firstName || "",
          lastName: profileData.lastName || "",
          phone: profileData.phone || "",
          email: user.email || "",
          location: profileData.location || "",
          state: profileData.state || "",
          city: profileData.city || "",
          pincode: profileData.pincode || "",
          avatar: profileData.avatar || "",
          bio: profileData.bio || ""
        });
      } else {
        // If no document exists, create one with basic info
        const basicProfile = {
          displayName: user.displayName || "User",
          firstName: "",
          lastName: "",
          email: user.email,
          phone: "",
          location: "",
          state: "",
          city: "",
          pincode: "",
          avatar: "",
          bio: "",
          createdAt: new Date()
        };
        
        await setDoc(doc(db, "users", user.uid), basicProfile);
        setProfile(basicProfile);
        setFormData(basicProfile);
      }
    } catch (error) {
      console.error("Error fetching profile:", error);
      // Fallback to basic user info if Firestore fails
      const localProfile = {
        displayName: user.displayName || "User",
        firstName: "",
        lastName: "",
        email: user.email,
        phone: "",
        location: "",
        state: "",
        city: "",
        pincode: "",
        avatar: "",
        bio: ""
      };
      
      setProfile(localProfile);
      setFormData(localProfile);
    }
  };

  const toggleUserMenu = () => {
    setShowUserMenu(!showUserMenu);
  };

  const handlePhotoUpload = async (event) => {
    const file = event.target.files[0];
    if (!file) {
      setUploadError("No file selected. Please choose an image.");
      return;
    }

    // Reset upload states
    setUploadError(null);
    setUploadSuccess(false);
    setCompressionInfo(null);
    setUploading(true);
    setUploadProgress(0);

    // Validate file type and size
    const validTypes = ['image/jpeg', 'image/png', 'image/gif', 'image/webp'];
    if (!validTypes.includes(file.type)) {
      setUploadError("Please select a valid image file (JPEG, PNG, GIF, or WebP).");
      setUploading(false);
      return;
    }

    if (file.size > 10 * 1024 * 1024) { // 10MB limit
      setUploadError("File size must be less than 10MB.");
      setUploading(false);
      return;
    }

    try {
      // Compression options
      const options = {
        maxSizeMB: 1,
        maxWidthOrHeight: 800,
        useWebWorker: true,
        onProgress: (progress) => {
          setUploadProgress(Math.round(progress));
        }
      };

      // Compress the image
      const compressedFile = await imageCompression(file, options);
      
      // Get compression info
      const compressionRatio = Math.round((1 - (compressedFile.size / file.size)) * 100);
      setCompressionInfo({
        originalSize: formatBytes(file.size),
        compressedSize: formatBytes(compressedFile.size),
        compressionRatio: compressionRatio
      });

      // Convert to base64 for storage
      const reader = new FileReader();
      reader.onload = async (e) => {
        try {
          const base64Image = e.target.result;
          
          // Update profile with new avatar
          const docRef = doc(db, "users", user.uid);
          await updateDoc(docRef, {
            avatar: base64Image,
            updatedAt: new Date()
          });
          
          setProfile(prev => ({ ...prev, avatar: base64Image }));
          setFormData(prev => ({ ...prev, avatar: base64Image }));
          setUploadSuccess(true);
        } catch (error) {
          console.error("Error updating profile:", error);
          setUploadError("Failed to update profile. Please try again.");
        } finally {
          setUploading(false);
        }
      };
      
      reader.onerror = () => {
        setUploadError("Failed to read the file. Please try again.");
        setUploading(false);
      };
      
      reader.readAsDataURL(compressedFile);
    } catch (error) {
      console.error("Error compressing image:", error);
      setUploadError("Failed to process the image. Please try again.");
      setUploading(false);
    }
  };

  const formatBytes = (bytes, decimals = 2) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const dm = decimals < 0 ? 0 : decimals;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSave = async () => {
    try {
      const docRef = doc(db, "users", user.uid);
      await updateDoc(docRef, {
        ...formData,
        updatedAt: new Date()
      });
      
      setProfile(formData);
      setIsEditing(false);
    } catch (error) {
      console.error("Error updating profile:", error);
      alert("Failed to update profile. Please try again.");
    }
  };

  const handleCancel = () => {
    setFormData({
      displayName: profile.displayName || "",
      firstName: profile.firstName || "",
      lastName: profile.lastName || "",
      phone: profile.phone || "",
      email: user.email || "",
      location: profile.location || "",
      state: profile.state || "",
      city: profile.city || "",
      pincode: profile.pincode || "",
      avatar: profile.avatar || "",
      bio: profile.bio || ""
    });
    setIsEditing(false);
  };

  const getInitials = (name) => {
    if (!name) return "U";
    return name
      .split(' ')
      .map(word => word[0])
      .join('')
      .toUpperCase()
      .slice(0, 2);
  };

  const handleLogoutClick = async () => {
    try {
      await signOut(auth);
      onLogout();
    } catch (error) {
      console.error("Error signing out:", error);
    }
  };

  return (
    <div className="hrm-dashboard">
      {/* Top Navigation Bar */}
      <nav className="top-nav">
        <div className="nav-content">
          <h1>My Profile</h1> {/* Changed from HRM Dashboard */}
          <div className="nav-actions">
            <div className="user-info" ref={userMenuRef}>
              <div 
                className="user-icon-circle"
                onClick={toggleUserMenu}
                role="button"
                tabIndex={0}
                aria-label="User menu"
                aria-expanded={showUserMenu}
              >
                {profile?.avatar ? (
                  <img 
                    src={profile.avatar} 
                    alt="Profile" 
                    className="user-icon-image"
                  />
                ) : (
                  <span className="user-icon">
                    {getInitials(profile?.displayName || user?.displayName)}
                  </span>
                )}
              </div>
              
              {showUserMenu && (
                <div className="user-dropdown-menu">
                  <div className="user-dropdown-header">
                    <div className="user-dropdown-avatar">
                      {profile?.avatar ? (
                        <img 
                          src={profile.avatar} 
                          alt="Profile" 
                        />
                      ) : (
                        getInitials(profile?.displayName || user?.displayName)
                      )}
                    </div>
                    <div className="user-dropdown-email">
                      {user?.email}
                    </div>
                  </div>
                  
                  <div className="user-dropdown-divider"></div>
                  
                  {/* Removed Edit Profile option from user menu */}
                  
                  <button 
                    className="user-dropdown-item"
                    onClick={() => {
                      // Handle settings navigation
                      setShowUserMenu(false);
                    }}
                  >
                    <span className="user-dropdown-icon"></span>
                    Settings
                  </button>
                  
                  <div className="user-dropdown-divider"></div>
                  
                  {/* Added Logout button */}
                  <button 
                    className="user-dropdown-item logout-button"
                    onClick={handleLogoutClick}
                  >
                    <span className="user-dropdown-icon"></span>
                    Logout
                  </button>

                   <button 
                    className="user-dropdown-item"
                    onClick={handleLogoutClick}
                  >
                    <span className="user-dropdown-icon"></span>
                    Back to Login
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>
      </nav>

      {/* Main Content */}
      <main className="hrm-main-content">
        <div className="profile-content">
          <div className="profile-card">
            {/* Profile Header */}
            <div className="profile-header">
              <div className="avatar-section">
                <div className="avatar-large">
                  {uploading ? (
                    <div className="upload-progress-container">
                      <div 
                        className="upload-progress-bar"
                        style={{ width: `${uploadProgress}%` }}
                      ></div>
                      <span className="upload-progress-text">
                        {uploadProgress}%
                      </span>
                    </div>
                  ) : profile?.avatar ? (
                    <img 
                      src={profile.avatar} 
                      alt="Profile" 
                    />
                  ) : (
                    getInitials(profile?.displayName || user?.displayName)
                  )}
                </div>
                
                {!uploading && (
                  <>
                    <input
                      type="file"
                      ref={fileInputRef}
                      onChange={handlePhotoUpload}
                      accept="image/*"
                      style={{ display: 'none' }}
                    />
                    <button
                      className="change-photo-btn"
                      onClick={() => fileInputRef.current?.click()}
                      disabled={uploading}
                    >
                      {profile?.avatar ? 'Change Photo' : 'Upload Photo'}
                    </button>
                  </>
                )}
                
                {compressionInfo && (
                  <div className="compression-info">
                    <div className="compression-message">
                      Compressed from {compressionInfo.originalSize} to {compressionInfo.compressedSize}
                    </div>
                    <div className="compression-progress">
                      <div 
                        className="compression-progress-bar"
                        style={{ width: `${100 - compressionInfo.compressionRatio}%` }}
                      ></div>
                    </div>
                  </div>
                )}
                
                {uploadError && (
                  <div className="upload-error">
                    {uploadError}
                  </div>
                )}
                
                {uploadSuccess && (
                  <div className="upload-success">
                    Photo updated successfully!
                  </div>
                )}
              </div>

              <div className="profile-info">
                <h2>{profile?.displayName || user?.displayName || "User"}</h2>
                <p>{user?.email}</p>
              </div>

              <div className="profile-actions">
                {isEditing ? (
                  <button 
                    className="cancel-edit-btn"
                    onClick={handleCancel}
                  >
                    Cancel
                  </button>
                ) : (
                  <button 
                    className="edit-profile-btn"
                    onClick={() => setIsEditing(true)}
                  >
                    Edit Profile
                  </button>
                )}
              </div>
            </div>

            {/* Profile Details */}
            <div className="profile-details">
              {/* Personal Information */}
              <div className="detail-section">
                <h3>Personal Information</h3>
                <div className="detail-grid">
                  <div className="detail-item">
                    <label>USER NAME</label>
                    {isEditing ? (
                      <input
                        type="text"
                        name="displayName"
                        value={formData.displayName}
                        onChange={handleInputChange}
                        className="form-input"
                        placeholder="Enter display name"
                      />
                    ) : (
                      <p>{profile?.displayName || "Not set"}</p>
                    )}
                  </div>

  

                  <div className="detail-item">
                    <label>Phone</label>
                    {isEditing ? (
                      <input
                        type="tel"
                        name="phone"
                        value={formData.phone}
                        onChange={handleInputChange}
                        className="form-input"
                        placeholder="Enter phone number"
                      />
                    ) : (
                      <p>{profile?.phone || "Not set"}</p>
                    )}
                  </div>

                  
                </div>
              </div>

              {/* Location Information */}
              <div className="detail-section">
                
                <div className="detail-grid">
                  <div className="detail-item">
                    <label>Address</label>
                    {isEditing ? (
                      <input
                        type="text"
                        name="location"
                        value={formData.location}
                        onChange={handleInputChange}
                        className="form-input"
                        placeholder="Enter your address"
                      />
                    ) : (
                      <p>{profile?.location || "Not set"}</p>
                    )}
                  </div>

                  <div className="detail-item">
                    <label>State</label>
                    {isEditing ? (
                      <input
                        type="text"
                        name="state"
                        value={formData.state}
                        onChange={handleInputChange}
                        className="form-input"
                        placeholder="Enter state"
                      />
                    ) : (
                      <p>{profile?.state || "Not set"}</p>
                    )}
                  </div>

                  <div className="detail-item">
                    <label>City</label>
                    {isEditing ? (
                      <input
                        type="text"
                        name="city"
                        value={formData.city}
                        onChange={handleInputChange}
                        className="form-input"
                        placeholder="Enter city"
                      />
                    ) : (
                      <p>{profile?.city || "Not set"}</p>
                    )}
                  </div>

                  <div className="detail-item">
                    <label>Pincode</label>
                    {isEditing ? (
                      <input
                        type="text"
                        name="pincode"
                        value={formData.pincode}
                        onChange={handleInputChange}
                        className="form-input"
                        placeholder="Enter pincode"
                      />
                    ) : (
                      <p>{profile?.pincode || "Not set"}</p>
                    )}
                  </div>
                </div>
              </div>

              {/* Save/Cancel Buttons */}
              {isEditing && (
                <div className="form-actions">
                  <button 
                    className="save-btn"
                    onClick={handleSave}
                  >
                    Save Changes
                  </button>
                  <button 
                    className="cancel-btn"
                    onClick={handleCancel}
                  >
                    Cancel
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}

export default Dashboard;