// components/PhoneLogin.js
import React, { useState, useEffect, useRef } from "react";
import { 
  RecaptchaVerifier, 
  signInWithPhoneNumber,
  signOut 
} from "firebase/auth";
import { auth } from "../firebase";
import "./Auth.css";

function PhoneLogin({ onLoginSuccess, onSwitchToRegister, onSwitchToEmail }) {
  const [step, setStep] = useState(1); // 1: enter phone, 2: enter OTP
  const [phoneNumber, setPhoneNumber] = useState("");
  const [countryCode, setCountryCode] = useState("+91");
  const [verificationCode, setVerificationCode] = useState("");
  const [confirmationResult, setConfirmationResult] = useState(null);
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const recaptchaVerifier = useRef(null);

  useEffect(() => {
    // Initialize reCAPTCHA verifier
    try {
      recaptchaVerifier.current = new RecaptchaVerifier(
        auth, 
        "recaptcha-container", 
        {
          size: "invisible",
          callback: () => {
            // reCAPTCHA solved, allow signInWithPhoneNumber
            console.log("reCAPTCHA solved");
          },
          "expired-callback": () => {
            // Reset reCAPTCHA
            console.log("reCAPTCHA expired");
            resetRecaptcha();
          }
        }
      );
    } catch (error) {
      console.error("Error initializing reCAPTCHA:", error);
      setErrors({ general: "Failed to initialize security verification. Please refresh the page." });
    }

    return () => {
      // Clean up reCAPTCHA when component unmounts
      if (recaptchaVerifier.current) {
        try {
          recaptchaVerifier.current.clear();
        } catch (error) {
          console.log("reCAPTCHA already cleared");
        }
      }
    };
  }, []);

  const resetRecaptcha = () => {
    if (recaptchaVerifier.current) {
      try {
        recaptchaVerifier.current.clear();
      } catch (error) {
        console.log("Error clearing reCAPTCHA:", error);
      }
    }
    
    try {
      recaptchaVerifier.current = new RecaptchaVerifier(
        auth, 
        "recaptcha-container", 
        {
          size: "invisible",
          callback: () => {
            console.log("reCAPTCHA solved");
          },
          "expired-callback": () => {
            console.log("reCAPTCHA expired");
            resetRecaptcha();
          }
        }
      );
    } catch (error) {
      console.error("Error reinitializing reCAPTCHA:", error);
    }
  };

  const handleSendCode = async (e) => {
    e.preventDefault();
    
    if (!phoneNumber.trim()) {
      setErrors({ phone: "Phone number is required" });
      return;
    }
    
    if (phoneNumber.trim().length < 10) {
      setErrors({ phone: "Please enter a valid phone number" });
      return;
    }
    
    setLoading(true);
    setErrors({});
    
    try {
      const fullPhoneNumber = countryCode + phoneNumber;
      
      // Verify reCAPTCHA is ready
      if (!recaptchaVerifier.current) {
        resetRecaptcha();
      }
      
      const confirmation = await signInWithPhoneNumber(
        auth, 
        fullPhoneNumber, 
        recaptchaVerifier.current
      );
      
      setConfirmationResult(confirmation);
      setStep(2);
    } catch (error) {
      console.error("Error sending verification code:", error);
      
      if (error.code === "auth/invalid-phone-number") {
        setErrors({ phone: "Invalid phone number format" });
      } else if (error.code === "auth/too-many-requests") {
        setErrors({ general: "Too many attempts. Please try again later." });
      } else {
        setErrors({ general: "Failed to send verification code. Please try again." });
      }
      
      // Reset reCAPTCHA on error
      resetRecaptcha();
    } finally {
      setLoading(false);
    }
  };

  const handleVerifyCode = async (e) => {
    e.preventDefault();
    
    if (!verificationCode.trim()) {
      setErrors({ code: "Verification code is required" });
      return;
    }
    
    if (verificationCode.trim().length < 6) {
      setErrors({ code: "Please enter a valid verification code" });
      return;
    }
    
    setLoading(true);
    setErrors({});
    
    try {
      const result = await confirmationResult.confirm(verificationCode);
      onLoginSuccess(result.user);
    } catch (error) {
      console.error("Error verifying code:", error);
      
      if (error.code === "auth/invalid-verification-code") {
        setErrors({ code: "Invalid verification code" });
      } else if (error.code === "auth/code-expired") {
        setErrors({ code: "Verification code has expired. Please request a new one." });
        setStep(1);
      } else {
        setErrors({ general: "Failed to verify code. Please try again." });
      }
    } finally {
      setLoading(false);
    }
  };

  const handleBackToPhoneEntry = () => {
    setStep(1);
    setVerificationCode("");
    setErrors({});
    
    // Sign out any partial authentication
    signOut(auth).catch(error => {
      console.log("Error during sign out:", error);
    });
  };

  return (
    <div className="auth-container">
      <div className="auth-card">
        <div className="auth-header">
          <h2>{step === 1 ? "Phone Sign In" : "Verify Code"}</h2>
          <p>
            {step === 1 
              ? "Enter your phone number to receive a verification code" 
              : "Enter the verification code sent to your phone"
            }
          </p>
        </div>
        
        {errors.general && (
          <div className="auth-error">
            {errors.general}
          </div>
        )}
        
        <div id="recaptcha-container"></div>
        
        {step === 1 ? (
          <form onSubmit={handleSendCode} className="auth-form">
            <div className="form-group">
              <label htmlFor="phone">Phone Number</label>
              <div className="phone-input-group">
                <select
                  value={countryCode}
                  onChange={(e) => setCountryCode(e.target.value)}
                  className="country-code-select"
                >
                  <option value="+1">+1 (US)</option>
                  <option value="+44">+44 (UK)</option>
                  <option value="+91">+91 (IN)</option>
                  <option value="+61">+61 (AU)</option>
                  <option value="+86">+86 (CN)</option>
                </select>
                <input
                  type="tel"
                  id="phone"
                  name="phone"
                  value={phoneNumber}
                  onChange={(e) => setPhoneNumber(e.target.value.replace(/\D/g, ''))}
                  className={errors.phone ? "error" : ""}
                  placeholder="Enter your phone number"
                  maxLength="15"
                />
              </div>
              {errors.phone && <span className="error-text">{errors.phone}</span>}
            </div>
            
            <button 
              type="submit" 
              className="auth-button primary"
              disabled={loading}
            >
              {loading ? "Sending Code..." : "Send Verification Code"}
            </button>
          </form>
        ) : (
          <form onSubmit={handleVerifyCode} className="auth-form">
            <div className="form-group">
              <label htmlFor="code">Verification Code</label>
              <input
                type="text"
                id="code"
                name="code"
                value={verificationCode}
                onChange={(e) => setVerificationCode(e.target.value.replace(/\D/g, ''))}
                className={errors.code ? "error" : ""}
                placeholder="Enter 6-digit code"
                maxLength="6"
              />
              {errors.code && <span className="error-text">{errors.code}</span>}
            </div>
            
            <div className="verification-actions">
              <button 
                type="button" 
                className="auth-button secondary"
                onClick={handleBackToPhoneEntry}
                disabled={loading}
              >
                Back
              </button>
              <button 
                type="submit" 
                className="auth-button primary"
                disabled={loading}
              >
                {loading ? "Verifying..." : "Verify Code"}
              </button>
            </div>
          </form>
        )}
        
        <div className="auth-divider">
          <span>Or</span>
        </div>
        
        <div className="auth-footer">
          <p>
            {step === 1 ? (
              <>
                Prefer to use email? <button onClick={onSwitchToEmail} className="link-button">Sign In with Email</button>
                <br />
                Don't have an account? <button onClick={onSwitchToRegister} className="link-button">Sign Up</button>
              </>
            ) : (
              <button onClick={handleBackToPhoneEntry} className="link-button">Use a different phone number</button>
            )}
          </p>
        </div>
      </div>
    </div>
  );
}

export default PhoneLogin;